  description: 'Ethnographies are resources generated by social scientists to understand the beliefs, values, and traditions of a culture. The problem is that they are labor intensive and difficult to build. The ultimate goal of Understanding Group Biases (UGB) is to do “big ethnography” using machine learning and NLP to automate the laborious task of anthropologists who spend years doing field research in order to study different cultures. UGB aims to detect cultural models using machine learning and natural language processing (NLP) tools applied to huge swaths of web data. With the growth of social media data this task can be automated and expanded to cover all the data we have access to. Although a wide range of tasks can be covered in addressing the challenges of this project, we divided these tasks into two distinct and major categories: 1: Automatically identify different groups within a given corpus; 2: Identifying the beliefs, biases, and cultural norms in each of the groups.',
