  image: LwLLImage,
  subtitle: 'Learning with Less Labels',
  description: 'Training accurate machine learning (ML) models such as deep neural networks currently requires lots of labeled data, which is often costly to annotate. The overarching goal of the CORAL (Combined Representations for Adept Learning) is  to develop revolutionary ML methods  that will achieve state-of-the-art performance on a broad range of tasks while requiring significantly fewer labeled instances of the data. Our technical approach is rooted in the intuition that drastic reduction of label-dependency for a given ML task must be compensated by introducing a strong inductive bias for that task.  We thus propose a common formal framework for reasoning about inductive biases using the notions of problem similarity and problem transport that allow us to translate results and knowledge from similar tasks into priors that can inform our target learning task. Furthermore, because it is unlikely that any individual approach will single-handedly achieve the stated objective, CORAL aims at developing a suite of complementary techniques to reduce ML’s dependence on massive labeled datasets through a novel framework to combine benefits across diverse approaches. We will integrate techniques via a single coherent framework inspired by Bayesian meta-learning that allows us to combine not just different supervised, unsupervised, and semi-supervised objectives, but also completely different architectures. CORAL is led by ISI with collaborators from USC, CMU, Caltech, Columbia, and UIUC.',
