  description: 'In today’s big data era, there are tremendous opportunities for exploiting available information to solve many different kinds of problems by building empirical, data-driven models of complex phenomena. Yet, building such models is generally an elaborate process, often requiring a data scientist trained in an evolving array of technologies. Subject matter experts have the domain knowledge required to deﬁne useful empirical models, but often lack the computer science expertise to construct them from scratch. Today, creating empirical models requires a subject-matter-expert/data-scientist team, making production and reﬁnement of models expensive and time-consuming. The goal of “Data Scientist in a Box” is to capture the expertise of a data scientist in a computer system, along with the often elusive skill set of data scientists who can communicate with subject matter experts without using technical jargon. Success would bring the revolutionary predictive and decision-making power of recent advances in machine learning models to the many people for whom these technologies are currently out of reach.',
