import React from 'react'
import { Paper, Typography } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'
import Input from './Input'

import { parseBibFile, normalizeFieldValue } from 'bibtex'
import { publications } from '../content/publications'

const bibFile = parseBibFile(publications)


const styles = theme => ({
  publications: {
    margin: '5vw',
    width:'90vw',
    padding: theme.spacing(3),
    '@media (min-width:600px)': {
      padding: theme.spacing(3),
      width:'90vw',
    },
    '@media (min-width:800px)': {
      padding: theme.spacing(3),
      width:'75vw',
    },
    '@media (min-width:1000px)': {
      padding: theme.spacing(4),
      width:'60vw',
    },
    '@media (min-width:1200px)': {
      padding: theme.spacing(5),
      width:'50vw',
    },
  },
  title: {
    color: '#333',
    fontFamily: 'Ubuntu, Open Sans, sans-serif',
    margin: theme.spacing(3, 0),
  },
  wrapper: {
    maxHeight: '50vh',
    overflow: 'auto',
  },
  publication: {
    margin: theme.spacing(5, 0),
    'h5': {
      color: '#333',
      fontFamily: 'Ubuntu, Open Sans, sans-serif',
    },
    'p': {
      color: '#333',
      fontFamily: 'Ubuntu, Open Sans, sans-serif',
    },
  },
})


class Publications extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      search: '',
    }
  }

  handleUpdate(search) {
    this.setState({search})
  }

  renderPublications() {
    const {classes} = this.props
    const {search} = this.state
    return Object.keys(bibFile.entries$).filter(key => {
      let entry = bibFile.getEntry(key)
      let year = (normalizeFieldValue(entry.getField('year')) || '').toLowerCase()
      let title = (normalizeFieldValue(entry.getField('title')) || '').toLowerCase()
      let author = (normalizeFieldValue(entry.getField('author')) || '').toLowerCase()
      return year.includes(search) || title.includes(search) || author.includes(search)
    }).map(key => {
      let entry = bibFile.getEntry(key)
      let year = normalizeFieldValue(entry.getField('year'))
      let title = normalizeFieldValue(entry.getField('title'))
      let author = normalizeFieldValue(entry.getField('author'))
      return (
        <div className={classes.publication} key={key}>
          <Typography component="h5" variant="h5">{title}</Typography>
          <Typography component="p" variant="body1">{author} ({year})</Typography>
        </div>
      )
    })
  }

  render() {
    const {classes} = this.props
    const {search} = this.state
    return (
      <React.Fragment>
        <Paper elevation={3} className={classes.publications}>
          <Typography
            component="h4"
            variant="h4"
            className={classes.title}>
            Publications
          </Typography>
          <Input text={search} label={'search publications'} updateText={this.handleUpdate.bind(this)} />
          <div className={classes.wrapper}>
            {this.renderPublications()}
          </div>
        </Paper>
      </React.Fragment>
    )
  }
}


export default withStyles(styles)(Publications)
