import React from 'react'
import { Paper, Typography } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'


const styles = theme => ({
  openings: {
    margin: '5vw',
    width:'90vw',
    padding: theme.spacing(3),
    '@media (min-width:600px)': {
      padding: theme.spacing(3),
      width:'90vw',
    },
    '@media (min-width:800px)': {
      padding: theme.spacing(3),
      width:'75vw',
    },
    '@media (min-width:1000px)': {
      padding: theme.spacing(4),
      width:'60vw',
    },
    '@media (min-width:1200px)': {
      padding: theme.spacing(5),
      width:'50vw',
    },
  },
  title: {
    color: '#333',
    fontFamily: 'Ubuntu, Open Sans, sans-serif',
    margin: theme.spacing(3, 0, 10, 0),
  },
})


class Openings extends React.Component {

  renderOpenings() {
    const {classes} = this.props
    const openings = [
      'Open position 1',
      'Open position 2',
      'Open position 3',
      'Open position 4',
      'Open position 5',
    ]
    return openings.map(opening => (
      <div className={classes.opening} key={opening}>
        <Typography component="p" variant="body1">
          {opening}
        </Typography>
      </div>
    ))
  }

  render() {
    const {classes} = this.props
    return (
      <React.Fragment>
        <Paper elevation={3} className={classes.openings}>
          <Typography
            component="h4"
            variant="h4"
            className={classes.title}>
            Open Positions
          </Typography>
          {this.renderOpenings()}
        </Paper>
      </React.Fragment>
    )
  }
}


export default withStyles(styles)(Openings)
